@import url(https://fonts.googleapis.com/css2?family=Fuzzy+Bubbles:wght@400;700&family=Poppins:ital,wght@1,200&display=swap);
*{box-sizing:border-box}body,h1,h2{margin:0}html{font-family:"Fuzzy Bubbles", "Poppins", sans-serif}.app{height:100vh;background-color:rgba(152,109,129,0.61)}.app__title{font-weight:700;font-size:38px;text-align:center;padding:40px 20px 20px}.app__container{margin:0 auto;max-width:1000px;padding:40px 40px;height:600px;display:grid;align-items:center;overflow-y:scroll;grid-template-columns:repeat(auto-fill, minmax(250px, 1fr));grid-gap:30px;gap:30px}

.person{height:250px;padding:40px 20px;border:1px solid rgba(0,0,0,0.18);background-color:rgba(255,255,255,0.08);border-radius:8px;box-shadow:0 0 12px rgba(0,0,0,0.25);transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.person:hover{-webkit-transform:scale(1.1);transform:scale(1.1);box-shadow:0 0 24px -3px rgba(0,0,0,0.29),0 0 26px -3px rgba(0,0,0,0.21)}.person__name{margin-bottom:25px}.person__age{font-size:20px;margin-bottom:25px;color:#670e0e}.person__partner{font-family:"Poppins", sans-serif;font-weight:bold;color:#1f001e}

